export function Pickupdropoff({ pickup_dropoff_route }) {
  return (
    <div className="mb-3 border border-gray-200 p-2 rounded-md">
      {/* <p className="text-yellow-400 font-bold text-xs">
        {pickup_dropoff_route.purpose}
      </p> */}
      <p className="text-sm font-bold">
        {pickup_dropoff_route.primary_text},{" "}
        <span className="font-normal">
          {pickup_dropoff_route.secondary_text}
        </span>
      </p>
      <p className="text-xs text-gray-400">
        {pickup_dropoff_route.route_detail}
      </p>
    </div>
  );
}
