import { useLocalStorage } from "./use_local_storage";

export function useCreateBookingLocalStorage(key) {
  const {
    setItem,
    getItem: getCreateBookingObj,
    removeItem: removeCreatedBookingObj,
  } = useLocalStorage(key);

  // save to local storge
  const setCreateBookingObj = (data) => {
    try {
      const item = window.localStorage.getItem(key);
      if (item) {
        const parsedData = JSON.parse(item);
        const newData = { ...parsedData, ...data };
        setItem(newData);
        return;
      }
      // otherwise
      setItem(data);
    } catch (error) {}
  };

  return { setCreateBookingObj, getCreateBookingObj, removeCreatedBookingObj };
}
